
import XButton from 'vux/src/components/x-button/index.vue'

export default {
props: ['result', 'from', 'index'],
data () {
return {
loading1: false,
loading2: false
}
},
components: {
XButton
},
methods: {
addManager (id) {
this.loading1 = true
this.$emit('on-add', {
id: id,
index: this.index,
cb: () => {
this.loading1 = false
}
})
},
deleteManager (id) {
this.$emit('on-delete', {
id: id,
index: this.index,
cb: () => {
this.loading2 = false
}
})
}
}
}



